.al{
  position: absolute;
  width: 100vw;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dd{
  width: 90%;
  word-wrap: break-word;
}
.b3 img{
  width: 30px;
}
.b3:hover{
  width: 140px;
}
.button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(174, 221, 243);
  border: none;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.164);
  cursor: pointer;
  transition-duration: .3s;
  overflow: hidden;
  position: relative;
}

.svgIcon {
  width: 12px;
  transition-duration: .3s;
}

.svgIcon path {
  fill: rgb(0, 0, 0);
}

.button:hover {
  width: 100px;
  border-radius: 50px;
  transition-duration: .3s;
  background-color: rgb(43, 195, 255);
  align-items: center;
}

.button:hover .svgIcon {
  width: 50px;
  transition-duration: .3s;
  transform: translateY(60%);
}
.b3:hover .svgIcon {
  width: 50px;
  transition-duration: .3s;
  transform: translateY(120%);
}
.button::before {
  position: absolute;
  top: -20px;
  color: rgb(0, 0, 0);
  transition-duration: .3s;
  font-size: 2px;
}

.b1::before{
  content: "Edit";
}
.b2::before{
  content: "delete";
}
.b3::before{
  content: "Add Note";
}

.button:hover::before {
  font-size: 13px;
  opacity: 1;
  transform: translateY(30px);
  transition-duration: .3s;
}


.myCard {
  background-color: transparent;
  width: 190px;
  height: 254px;
  perspective: 1000px;
}

.title {
  width: 100%;
  font-size: 1.5em;
  font-weight: 900;
  text-align: center;
  margin: 0;
  word-wrap: break-word;
}

.innerCard {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.myCard:hover .innerCard {
  transform: rotateY(180deg);
}

.frontSide,
.backSide {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 1rem;
  color: white;
  font-weight: 700;
}
.bs{
  justify-self: flex-end;
}
.frontSide {
  background: linear-gradient(43deg, rgb(71 219 235) 0%, #50a6be 46%, rgb(44 126 255) 99%);
}

.backSide {
  background-image: linear-gradient(160deg, #4f9bbe 0%, #231ccd 100%);
}

.backSide {
  transform: rotateY(180deg);
}



@keyframes animate {
  0% {
    opacity: 0.3;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0.3;
  }
}




.form {
  background-color: #fff;
  display: block;
  padding: 1rem;
  max-width: 350px;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.form-title {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 600;
  text-align: center;
  color: #000;
}

.input-container {
  position: relative;
}

.input-container input, .form button {
  outline: none;
  border: 1px solid #e5e7eb;
  margin: 8px 0;
}

.input-container input {
  background-color: #fff;
  padding: 1rem;
  padding-right: 3rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  width: 300px;
  border-radius: 0.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.submit {
  display: block;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  background-color: #4F46E5;
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  width: 100%;
  border-radius: 0.5rem;
  text-transform: uppercase;
}

.signup-link {
  color: #6B7280;
  font-size: 0.875rem;
  line-height: 1.25rem;
  text-align: center;
}

.signup-link a {
  text-decoration: underline;
}
.log{
  width: 100vw;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}